/************************************************

Stylesheet: Global Stylesheet

*************************************************/
/*********************
COMMON
*********************/
.module {
	padding-top: 1.5em;
	padding-bottom: 1.5em;
	
	@include breakpoint(medium) {
		padding-top: 4.25em;
		padding-bottom: 4.25em;	
	}
	
&.no-top-padding {
	padding-top: 0;
}

&.no-bottom-padding {
	padding-bottom: 0;
}
	
}

// a:not(.lm-link):hover {
// 	text-decoration: underline;
// }

button,
.button {
	cursor: pointer;
	font-weight: 600;
	line-height: 1.6;
}

.unbroken-link {
	white-space: nowrap;
}

.underlined {
	text-decoration: underline;
}

.small-caps {
	font-family: $font-body;
	font-size: rem-calc(19);
	font-weight: bold;
	text-transform: uppercase;
	color: $brand-green;
	letter-spacing: 1px;
}

.pipe {
	height: 5px;
	width: 102px;
	background-color: $brand-green;
}

a.lm-link {
	display: inline-block;
	font-size: rem-calc(19);
	font-weight: bold;
	position: relative;
	padding-bottom: .5em;
	transition: all .3s ease;
	
&:hover {
	color: $link-hover-green;
}
	
&:after {
	content: '';
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 5px;
	background-color: $brand-green;
	transition: all .3s ease;
}

&:hover:after {
	transform: translateY(-6px);
	background-color: $link-hover-green;
}
	
}

.cta-donate {
	
	a.inner {
		display: block;
		height: 100%;
		background-color: $brand-green;
		color: $white;
		
		img {
			max-width: 46px;
			
			@include breakpoint(medium) {
				max-width: 100%;
			}
			
		}
		
		.text-wrap {
			padding-top: 1.1em;
		    padding-bottom: .1em;
			padding-left: .5rem;
			padding-right: .5rem;			    
		
			h3 {
				line-height: 1.194;
				margin-bottom: 0;
				font-size: rem-calc(36);
				font-weight: 800;
				font-family: $font-body;
			}
			
			p {
				line-height: 1.6;
			}
		
		}
		
		.arrow-box {
			min-height: 100%;
			background-color: $brand-brown;
			display: flex;
			align-items: center;
			transition: .3s ease;
			
			svg {
				max-width: 19px;
				
				@include breakpoint(medium) {
					max-width: 100%;
				}
					
			}
			
		}
		
		&:hover {
			text-decoration: none;
			
			.arrow-box {
				background-color: $brand-green;
			}
			
		}
		
	}
}

.green-bg,
.dark-green-bg {
	color: $white;
	
	a,
	h1,
	.h1,
	h2,
	.h2 {
		color: $white;
	}
	
}

.green-bg {
	background-color: $brand-green;
}

.dark-green-bg {
	background-color: $brand-green;
}

/*********************
TYPOGRAPHY
*********************/
h2,
.h2 {
	color: $brand-green;
	margin-bottom: 0.33em;
}

.black-weight {
	font-weight: 800;
}

p,
.copy-wrap,
.text-wrap {
	font-size: rem-calc(14);
	line-height: 1.94;
	
	span.large-green-text {
		font-size: rem-calc(19);
		line-height: 1.782;
		font-weight: 500;
		color: $brand-green;
		display: inline-block;
		padding-bottom: 0.65em;
	}
	
	@include breakpoint(medium) {
		font-size: rem-calc(19);
		
		span.large-green-text {
			font-size: rem-calc(23);
		}
		
	}
	
}

/*********************
POSTS & CONTENT STYLES
*********************/
.page-title {
	
	// Remove Foundation vcard styling from author page titles
	.vcard {
		border: 0px;
		padding: 0px;
	}
}

// Post meta  
.byline {
	color: #292929;
	font-size: rem-calc(20);
	letter-spacing: -0.4px;
	time {}
}

// Entry content  
.entry-content {
	
	img {
		max-width: 100%;
		height: auto;
	}

	.alignleft, img.alignleft { 
		margin-right: 1.5em; 
		display: inline; 
		float: left; 
	}
	.alignright, img.alignright { 
		margin-left: 1.5em; 
		display: inline; 
		float: right; 
	}
	.aligncenter, img.aligncenter { 
		margin-right: auto; 
		margin-left: auto; 
		display: block; 
		clear: both; 
	}
			
	video, object {
		max-width: 100%;
		height: auto;
		}

	pre {
		background: #eee;
		border: 1px solid #cecece;
		padding: 10px;
		}

} 

.wp-caption {
	max-width: 100%;
	background: #eee;
	padding: 5px;

	// Images inside wp-caption  
	img {
		max-width: 100%;
		margin-bottom: 0;
		width: 100%;
	}

	p.wp-caption-text {
		font-size: 0.85em;
		margin: 4px 0 7px;
		text-align: center;
	}
} 

// Add Foundation styling to password protect button
.post-password-form input[type="submit"] {
	@include button;
}

/*********************
IMAGE GALLERY STYLES
*********************/
.gallery {
	max-width: 100%;
	margin-left: auto;
	margin-right: auto;
	margin-right: -15px;
	margin-left: -15px; 
}
.gallery::before, .gallery::after {
	content: ' ';
	display: table; 
}
.gallery::after {
	clear: both; 
}
.gallery.gallery-columns-1 > dl {
	@include grid-column;
}
.gallery.gallery-columns-2 > dl {
	@include grid-column(1 of 2);
}
.gallery.gallery-columns-3 > dl {
	@include grid-column(1 of 3);
}
.gallery.gallery-columns-4 > dl {
	@include grid-column(1 of 4);
}
.gallery.gallery-columns-5 > dl {
	@include grid-column(1 of 5);
}
.gallery.gallery-columns-6 > dl {
	@include grid-column(1 of 6);
}
.gallery.gallery-columns-7 > dl {
	@include grid-column(1 of 7);
}
.gallery.gallery-columns-8 > dl {
	@include grid-column(1 of 8);
}
.gallery dl {
	width: 100%;
	float: left;
	padding-left: 0.9375rem;
	padding-right: 0.9375rem; 
}
.gallery dl:last-child:not(:first-child) {
  float: right; 
 }

